.very-clever-outer {
  min-height: calc(100vh - 4rem);
  width: calc(100vw - 2rem);
  background-color: #080808;
  color: white;
  padding: 2rem 1rem;

  nav {
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    background-color: darkgrey;
    button {
      background-color: darkgrey;
      -webkit-appearance: none;
      border: none;
      height: 2em;
      padding: 0 2em;
      outline: 0;

      &.undo {
        -moz-transform: scaleX(-1);    /* Gecko */
        -o-transform: scaleX(-1);      /* Opera */
        -webkit-transform: scaleX(-1); /* Webkit */
        transform: scaleX(-1);         /* Standard */
      }
      &.restart {
        float: right;
      }
    }
  }
}
.very-clever {
  position: relative;

  .value-selection,.bonus-splash {
    top: 0;
    left: 0;
    text-align: center;
    position: fixed;
    z-index: 2;
    width: 100%;
    height: 100%;

    .value-selection-overlay,.bonus-splash-overlay {
      top: 0;
      position: absolute;
      z-index: 2;
      background-color: black;
      opacity: 0.8;
      width: 100%;
      height: 100%;
    }
    .value-selection-inner,.bonus-splash-inner {
      position: relative;
      z-index: 3;
      top: 20vh;
      display: inline-block;

      .value {
        cursor: pointer;
        background-color: black;
        border: 1px solid white;
        width: 80vw;
        font-size: 2rem;
        line-height: 10vh;
        -webkit-transition-duration: 0.4s; /* Safari */
        transition-duration: 0.4s;

        &:first-child {
          border-radius: 1rem 1rem 0 0;
        }

        &:last-child {
          border-radius: 0 0 1rem 1rem;
        }

        &:hover {
          color: black;
          background-color: white;
        }
      }

      .bonus {
        font-size: 4em;
        animation: disco 2s linear infinite;
        border-width: 5px;
      }
    }
  }

  .round-overview {
    font-size: 2rem;
    font-weight: bold;

    button {
      margin-left: 1rem;
      outline: 0;
      background-color: #4CAF50; /* Green */
      border: 2px solid #4CAF50; /* Green */
      border-radius: 10px;
      color: white;
      padding: 0.5rem 1rem;
      text-align: center;
      text-decoration: none;
      display: inline-block;
      font-size: 16px;
      -webkit-transition-duration: 0.4s; /* Safari */
      transition-duration: 0.4s;
      &:hover {
        background-color: white;
        color: #4CAF50;
      }
    }
  }

  @media screen and ("min-width: 420px") {
    .grid-box-outer {
      width: 50%;
      display: inline-block;
    }
  }
  .grid-row {
    box-sizing: border-box;
    border: 4px solid white;
    border-radius: 0.5em;
    padding: 0.5em;
    margin-bottom: 1.5rem;
    grid-template-columns: 1fr;
    grid-template-rows: 2rem;
    grid-auto-columns: 1fr;
    grid-auto-flow: column;
    grid-column-gap: 2px;
  }

  .grid-box {
    display: grid;
    box-sizing: border-box;
    border: 4px solid white;
    border-radius: 0.5em;
    padding: 0.5em;
    margin-bottom: 1.5rem;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 2px;

    .grid-item {
      place-self: center;
      width: 100%;

      .box,.bonus {
        width: 100%;
      }

      &.grid-arrow {
        width: 1rem;
      }
    }
  }

  .grid-bonus-row {
    padding: 0.5em 0;
    margin-bottom: 1rem;
    grid-template-columns: 2rem;
    grid-template-rows: 2rem;
    grid-auto-columns: 2rem;
    grid-auto-flow: column;
    grid-column-gap: 10px;

    .grid-item .content {
      font-size: 1.75rem;
    }

    svg.icon {
      width: 2rem;
    }
  }

  .grid-row,.grid-bonus-row {
    display: grid;

    .grid-item {
      place-self: center / center;
      position: relative;

      .bonus,.points {
        text-align: center;
        position: absolute;
        z-index: 1;
      }
      .bonus {
        width: 2rem;
        bottom: -2rem;
        left: calc(50% - 2rem + 0.75rem);
      }
      .points {
        width: 1rem;
        padding: 0.35em;
        top: -1.5rem;
        border-radius: 1em;
        left: calc(50% - 1rem + 0.175rem);
      }
    }

    .box {
      width: 100%;
      height: 100%;
    }

    &.growing {
      .grid-item:not(:last-child):after {
        content: '<';
        position: absolute;
        z-index: 1;
        color: white;
        font-weight: 900;
        text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
        font-size: 1.5em;
        right: -0.4rem;
        top: 0.25rem;
      }
    }
  }

  .box {
    position: relative;
    color: black;
    background-color: white;
    display: inline-block;
    border-radius: 0.25em;
    box-sizing: border-box;
    border: 2px solid white;
    width: 2em;
    line-height: 2em;
    vertical-align: middle;
    text-align: center;

    &.highlight {
      animation: flickery 1s linear;
    }

    &.crossed:after {
      content: 'âœ˜';
      font-size: 2em;
      color: black;
      z-index: 1;
      position: absolute;
      left: calc(50% - 0.4em);
      top: 0;
    }
    &.bonus {
      margin: 0.25em;
      width: 2em;
      line-height: 1.5em;
      color: white;
      .content {
        padding: 0.25em 0;
        font-weight: bold;
      }
    }
    .picked {
      color: black;
      font-weight: bold;
      font-size: 1.5em;
    }
  }

  .points-total {
    font-size: 2rem;
    margin: 2rem 0;
  }
}

@keyframes flickery {
  0%   {opacity: 1}
  50%  {opacity: 0.25}
  100% {opacity: 1}
}

@keyframes disco {
  0%   {border-color: green}
  25%  {border-color: red}
  50%  {border-color: yellow}
  75% {border-color: blue}
  100% {border-color: green}
}
