.sw-update {
  text-align: center;
  color: white;
  position: fixed;
  z-index: 1000;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  .sw-update-overlay {
    height: 100%;
    width: 100%;
    opacity: 0.8;
    background-color: black;
    position: fixed;
    z-index: 1;
  }
  .sw-update-content {
    font-size: 3em;
    position: relative;
    z-index: 2;
    top: calc(50vh - 3em);
  }
}