
.App ul {
  position: relative;
  padding-left: 3em;
  margin-top: 2em;

  &:before {
    content: '';
    position: absolute;
    left: 0.5em;
    top: 1.2em;
    bottom: 1em;
    width: 0.2em;
    background-color: darkgreen;
  }

  li {
    list-style-type: none;
    position: relative;
    dl {
      display: flex;
    }
    dt {
      display: flex;
      flex: 1;
    }
    dd {
      display: flex;
      flex: 4;
    }
  }

  li:not(:last-child) {
    margin-bottom: 2em;
  }

  li:before {
    content: '';
    position: absolute;
    border-radius: 100%;
    width: 1em;
    height: 1em;
    background-color: darkgreen;
    left: -2.9em;
    top: 0.3em;
  }
}
