@import '../../node_modules/react-staged/dist/assets/staged.scss';

.App {
  background-color: #2e2b2b;

  footer {
    width: 100vw;
    position: absolute;
    background-color: #2e2b2b;
    bottom: 0;

    button {
      background: transparent;
      color: #fff;
      border: 1px solid darkgrey;
      padding: 1em 0;
      width: 50%;
      outline: 0;
      font: inherit;

      &.selected {
        background-color: rgba(255, 255, 255, 0.2);
      }
    }
  }

  .App-content {
    position: relative;

    h1 {
      position: absolute;
      left: 0;
      color: black;
      width: auto;
      padding: 0.5em 1em;
      background-color: whitesmoke;
      box-shadow: 2px 2px 4px 2px black;
    }

    .preview {
      position: relative;
      height: 100vh;
      background-repeat: no-repeat;
      background-position: center;
      -webkit-background-size: cover;
      -moz-background-size: cover;
      -o-background-size: cover;
      background-size: cover;
    }
    .preview-again {
      background-image: url("/preview-again.jpg");
    }

    .preview-sit {
      background-image: url("/preview-sit.jpg");
    }

    .preview-veryClever {
      background-image: url("/preview-veryClever.jpg");
    }

    .preview-decrypto {
      background-image: url("/preview-decrypto.jpg");
    }

    a {
      font-size: 5vw;
      @media (orientation: landscape) {
        font-size: 5vh;
      }

      position: absolute;
      right: 1em;
      bottom: 1em;
      text-decoration: none;
      color: white;
      background-color: #00a900;
      border-radius: 1em;
      padding: 0.5em 1em;
      box-shadow: 4px 4px 2px 0 black;
    }
  }

  .App-logo {
    height: 1.5em;
    padding: 0.25em;
  }

  .App-header {
    position: absolute;
    z-index: 1;
    right: 0;
  }
}
