$bg-me: white;
$bg-enemy: #1f1e20;
$bg-code: #9d9d9d;
$color-me: black;
$color-enemy: white;
$color-code: darkred;

.decrypto {
  min-height: calc(100vh - 6em);
  padding: 3em 0.5em 3em;
  background-color: $bg-me;
  color: $color-me;
  text-align: center;

  .content {
    position: relative;
    z-index: 2;
  }

  &.alarm:before,&.enemy.success:before {
    box-shadow: inset 0 0 20px red;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    content: " ";
  }
  &.enemy.alarm:before {
    box-shadow: unset;
  }
  &.enemy.success:before {
    box-shadow: inset 0 0 20px green;
  }

  &.enemy {
    background-color: $bg-enemy;
    color: $color-enemy;

    .select-overlay .select-overlay-inner .value {
      color: $color-enemy;
      background-color: $bg-enemy;
      border: 1px solid $color-enemy;

      &:hover {
        color: $bg-enemy;
        background-color: $color-enemy;
      }
    }

    .navigation button {
      background-color: $bg-me;
      color: $color-me;
    }

    .guessing,.guessed {
      .guess .code-guess {
        border-color: $color-enemy;
      }
    }

    .qr-scanning button {
      color: $color-enemy;
    }

    .knowledge table thead td {
      border: 1px solid $color-enemy;
    }
  }

  .select-overlay {
    top: 0;
    left: 0;
    text-align: center;
    position: fixed;
    z-index: 2;
    width: 100%;
    height: 100%;

    .select-overlay-outer {
      top: 0;
      position: absolute;
      z-index: 3;
      background-color: black;
      opacity: 0.8;
      width: 100%;
      height: 100%;
    }
    .select-overlay-inner {
      position: relative;
      z-index: 4;
      top: 20vh;
      display: inline-block;

      .value {
        cursor: pointer;
        color: $color-me;
        background-color: $bg-me;
        border: 1px solid $color-me;
        width: 80vw;
        font-size: 2rem;
        line-height: 10vh;
        -webkit-transition-duration: 0.4s; /* Safari */
        transition-duration: 0.4s;

        &:first-child {
          border-radius: 1rem 1rem 0 0;
        }

        &:last-child {
          border-radius: 0 0 1rem 1rem;
        }

        &:hover {
          color: $bg-me;
          background-color: $color-me;
        }
      }
    }
  }

  .qr-code,.qr-scanning {
    height: 100vh;
    width: 100vw;
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;

    button {
      position: fixed;
      font-size: 3em;
      z-index: 1;
      border: none;
      outline: 0;
      background-color: unset;
    }

    @media (orientation: portrait) {
      button {
        bottom: 1em;
        width: 50%;
        left: 25%;
      }
    }

    @media (orientation: landscape) {
      button {
        left: 1em;
        top: calc(50% - 0.5em);
      }
    }
  }

  .qr-code {
    background-color: $bg-me;
  }

  .qr-scanning {
    background-color: $bg-enemy;

    .scanner {
      video {
        width: 100vw;
        height: auto;
      }
      canvas {
        display: none;
      }
    }
  }

  .generate-code,.scan-code {
    button {
      margin: 0.25em;
      font-size: 2em;
      box-shadow: 2px 2px 2px 2px black;
    }
  }

  button .description {
    font-size: 0.25em;
  }

  .knowledge {
    overflow-x: scroll;

    table {
      width: 100%;
      border-collapse: collapse;

      thead {
        td {
          border: 1px solid $color-me;
        }
      }
    }
  }

  .navigation {
    button {
      z-index: 3;
      position: fixed;
      top: 0;
      right: 0;
      padding: 0.25em 0.5em;
      border-radius: 0 0 0 1em;
      background-color: $bg-enemy;
      border: none;
      color: $color-enemy;
      font-size: 1.5em;
      outline: 0;

      &.restart {
        top: unset;
        bottom: 0;
        border-radius: 1em 0 0 0;
      }
    }
  }

  .round {
    position: absolute;
    top: -1.25em;
    left: 0.25em;
    font-size: 2em;
    font-weight: bold;
  }

  .code-words-selection {
    .code {
      font-size: 3em;
      color: $color-code;
      padding: 0.25em 0.5em;
      > span {
        background-color: $bg-code;
        border-radius: 0.5em;
        padding: 0.25em 0.5em;
        font-weight: bold;
      }
    }

    form {
      margin: 0.5em 0;
      input {
        font-size: 2em;
        border-width: 0.25em;
        width: calc(100% - .5em);

        &.is-invalid {
          border-color: red;
        }
      }

      .invalid-feedback {
        display: block;
        color: red;
      }

      button {
        margin: 0.25em;
        font-size: 2em;
        box-shadow: 2px 2px 2px 2px black;
      }
    }
  }

  .guessing,.guessed {
    padding: 0.5em;
    font-size: 2em;

    .guess {
      padding: 0.25em 0;
      text-align: left;

      .code-word {
        width: calc(100% - 1.5em);
        overflow-x: scroll;
        display: inline-block;
      }

      .code-guess {
        position: absolute;
        right: 0.5em;
        border: 2px solid $color-me;
        padding: 0 0.25em;
        border-radius: 1em;

        &.invalid {
          animation: flickery2 2s infinite linear;
        }

        &.valid {
          background-color: green;
        }
      }

      .code-was {
        position: absolute;
        right: 0.5em;
        font-size: 0.5em;
      }
    }

    button {
      margin: 0.25em;
      font-size: 1em;
      box-shadow: 2px 2px 2px 2px black;
    }
  }

  .open-qr-code {
    text-align: center;
    margin: 0.25em;
    font-size: 2em;
    box-shadow: 2px 2px 2px 2px black;
  }

  .result-overlay {
    position: fixed;
    text-align: center;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    font-size: 3em;
    background-color: #000a;
    z-index: 4;

    > span {
      top: 40%;
      position: relative;
      z-index: 4;
      color: white;
      background-color: #000a;
      border-radius: 0.5em;
      padding: 0.25em;
    }
  }
}

@keyframes flickery2 {
  0%   {background-color: #f00f}
  40%  {background-color: #f00f}
  50%  {background-color: #f000}
  60%  {background-color: #f00f}
  100% {background-color: #f00f}
}
