
$bg-color: #38144a;
$color: lightgreen;

.mission-control {
  background-color: $bg-color;
  color: $color;
  height: 90vh;
  padding: 5vh 5vw;

  .mission-content {
    padding: 2em;
    border: 5px solid;
    border-radius: 1em;
    display: flex;
  }

  .mission-list {
    border: 1px solid;
    border-radius: 1em;
    min-width: 10em;
    ul {
      list-style-type: none;
      -webkit-margin-before: 0;
      -webkit-margin-after: 0;
      -webkit-padding-before: 1em;
      -webkit-padding-after: 1em;
      -webkit-padding-start: 0;
      li {
        padding: 0.5em 1em;
        list-style-type: none;
        cursor: pointer;

        &:hover {
          background-color: $color;
          color: $bg-color;
        }

        &.selected {
          background-color: lighten($bg-color, 10);
          color: $color;
          cursor: default;
        }
      }
    }
  }

  .mission-desc {
    padding: 1em;
    margin-left: 1em;
    border: 1px solid;
    border-radius: 1em;
    width: 100%;
  }
}
