.again {
  min-height: calc(100vh - 5em);
  padding: 3.5em 2% 1.5em 2%;
  .box {
    width: 95%;
    &.box-flicker {
      animation: flickery 2s infinite linear;
    }
    svg {
      width: 100%;
    }
  }
  .cube {
    display: inline-block;
    width: 1.5em;
    margin: 0 0.25em;
  }
  .again-turn {
    color: white;
    .abort,.finish {
      background-color: black;
      position: fixed;
      z-index: 2;
      height: 2em;
      width: 2em;
      top: 0;
      padding: 0.5em 1em;
    }
    .abort {
      left: 0;
      border-radius: 1em 0;
    }
    .finish {
      right: 0;
      border-radius: 0 1em;
    }
    .skip {
      top: 0;
      right: 0;
      padding: 0.5em 1em;
      position: fixed;
      z-index: 2;
    }
    .main-control {
      z-index: 2;
      background-color: black;
      position: fixed;
      top: 0;
      left: calc(50% - 2.5em);
      width: 4em;
      padding: 0.5em;
      border-radius: 1em;
    }
  }
  .again-turns {
    color: white;
    .again-turn {
      margin-top: 0.5em;
      > div {
        position: absolute;
        display: inline-block;
      }
      .turn-chosen {
        left: 2.5em;
      }
      .turn-success {
        left: 7em;
        > div {
          display: inline-block;
        }
        .success-arrow {
          width: 1.3em;
          margin-right: 0.5em;
        }
        .box {
          margin: 0 0.2em;
          width: 1.5em;
          display: inline-block;
          svg {
            width: unset;
          }
        }
      }
    }
  }
  .again-letters,.again-row-points-1,.again-row-points-2 {
    >div {
      display: inline-block;
      width: 6.66%;
    }
   }
  .again-color-points-1,.again-color-points-2 {
    >div {
      display: inline-block;
      width: 13.32%;
    }
  }
  .again-color-points {
    margin-top: 0.5em;
    width: 50%;
  }
  .again-letters {
    margin-top: 1em;
  }
  .again-row,.again-total,.again-color-points-1,.again-color-points {
    display: inline-block;
    >div {
      @media (max-width: 900px) { margin-top: 0 }
      @media (max-width: 700px) { margin-top: -2px }
      @media (max-width: 300px) { margin-top: -4px }
    }
  }
  .again-row {
    width: 6.66%;
  }
  .again-row.row-alert {
    animation: flickery 2s infinite linear;
  }
  .again-jokers {
    background-color: white;
    margin: 1em 0;
    padding: 1.5% 2% 1% 2%;
    border-radius: 0.5em;
    >div {
      display: inline-block;
      width: 8.33%;
    }
  }
  .again-total {
    color: white;
    vertical-align: top;
    margin-top: 0.5em;
    .again-total-points {
      color: black;
      margin-left: 1em;
      background-color: white;
      border-radius: 1em;
      padding: 0.2em 0.5em;
      display: inline-block;
    }
    .again-last-turn {
      margin-top: 1em;
      > div {
        margin-left: 1em;
        display: inline-block;
      }
    }
  }
}

.again-splash {
  animation: fading 0.5s cubic-bezier(1,.01,1,.01);
  opacity: 0;
  text-align: center;
  color: white;
  .splash-overlay {
    height: 100%;
    width: 100%;
    opacity: 0.8;
    background-color: black;
    position: fixed;
    z-index: 5;
  }
  .splash-content {
    position: fixed;
    z-index: 6;
    top: calc(50vh - 3em);
    left: calc(50vw - 3em);
    svg {
      width: 6em;
    }
  }
}

@keyframes fading {
  0% { opacity: 1 }
  100% { opacity: 0 }
}

.again-popup {
  text-align: center;
  .popup-overlay {
    height: 100%;
    width: 100%;
    opacity: 0.8;
    background-color: black;
    position: fixed;
    z-index: 3;
  }
  .popup-content {
    position: fixed;
    color: white;
    top: calc(50vh - 1.75em);
    left: calc(50vw - 5.125em);
    background-color: black;
    z-index: 4;
    padding: 1em;
    border: 2px solid white;
    border-radius: 1em;
    font-size: 1.5em;
    > div {
      display: inline-block;
      width: 1em;
      margin-left: 0.5em;
    }
  }
}

.again-selection {
  background-color: #00a900;
  height: calc(100vh - 4em);
  padding: 2em;
  animation: background-transition 15s infinite linear;
  select {
    font-size: 1.5rem;
    display: inline-block;
    width: 100%;
    height: calc(2.75rem + 2px);
    padding: .375rem 1.75rem .375rem .75rem;
    line-height: 1.5;
    color: #495057;
    vertical-align: middle;
    background: #fff url(/select.svg) no-repeat right .75rem center;
    background-size: 8px 10px;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    -webkit-appearance: none;
    -moz-appearance: none;
    box-shadow: 2px 2px 5px 1px rgba(0,0,0,0.3);
    font-weight: 900;
  }
}

@keyframes background-transition {
  0%   {background-color: darkgreen}
  50%  {background-color: darkred}
  100% {background-color: darkgreen}
}

@keyframes flickery {
  0%   {opacity: 1}
  50%  {opacity: 0.5}
  100% {opacity: 1}
}