.admin, .admin-entry {
  position: absolute;
  left: 0;
  z-index: 1000;
}

.admin-entry {
  bottom: 0;
  width: 2em;
  height: 2em;
}

.admin {
  overflow: scroll;
  min-height: 100vh;
  width: 100vw;
  background-color: #2e2b2b;
  color: white;
  top: 0;

  .console-error p {
    background-color: darkred;
    border: 1px solid red;
  }
}