@mixin sit-green-button {
  outline: 0;
  background-color: #4CAF50; /* Green */
  border: 2px solid #4CAF50; /* Green */
  border-radius: 10px;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  &:hover {
    background-color: white;
    color: black;
  }
}

.spade-is-trump {
  .configs {
    padding: 1rem;
  }
  table, th, td {
    border-right: 1px solid #9d9d9d;
    border-left: 1px solid #9d9d9d;
    border-collapse: collapse;
  }
  th, td {
    min-width: 3em;
    text-align: center;
    border-bottom: 1px solid #ddd;
    margin: 0;
    padding: 0;
    input {
      width: 4em;
      padding: 0.5em;
    }
  }
  th {
    height: 50px;
    background-color: #1f1e20;
    color: white;
    &.card-number {
      width: 6em;
    }
  }
  table {
    border-spacing: 5px;
    width: 100%;
  }
  td {
    height: 3em;
    vertical-align: middle;
    &.active {
      box-shadow: 0 5px 16px 5px rgba(0, 0, 255, 0.5), 0 -5px 20px 5px rgba(0, 0, 255, 0.5);
      border-radius: 10px;

      .points-success {
        border-radius: 0 0 0 5px;
      }
      .points-failed {
        border-radius: 0 0 5px 0;
      }
    }
    .error-round {
      color: red;
      padding-left: 5px;
    }
    .winner {
      color: darkgoldenrod;
      padding-left: 5px;
    }
  }
  tr {
    &.disabled {
      background-color: darkgrey;
      opacity: 0.5;
    }
    &:nth-child(even) {
      background-color: #f2f2f2;
    }
    &.sum {
      background-color: #b8f3b8;
    }
  }
  form.startup {
    margin-bottom: 3rem;
    .form-field {
      padding-bottom: 1em;
    }
    input {
      width: 100%;
      padding: 12px 20px;
      margin: 8px 0;
      outline: 0;
      box-sizing: border-box;
      &.is-invalid {
        border: 2px solid red;
      }
      border-radius: 10px;
      background-color: #f4f4f4;
      color: black;
      font-size: 2em;
      &:focus {
        border: 3px solid #555;
        &.is-invalid {
          border: 2px solid red;
        }
      }
    }
    span.invalid-feedback {
      color: red;
    }
    button {
      @include sit-green-button;
      float: right;
    }
    @media (min-width: 480px) {
      .form-control {
        vertical-align: top;
        display: inline-block;
        width: calc(50% - 1em);
        margin-right: 1rem;
      }
    }
  }
  .points-success, .points-failed {
    width: 50%;
  }
  .points-tip {
    padding: 0.5em;
  }
  .points-lost {
    background-color: #dc0000;
  }
  .points-earned {
    background-color: #00a900;
  }
  .points-failed {
    background-color: red;
    border: 2px solid red;
    color: white;
    padding: 0.5em;
  }
  .points-success {
    background-color: darkgreen;
    border: 2px solid darkgreen;
    color: white;
    padding: 0.5em;
  }
  nav {
    width: 100%;
    background-color: black;
    button {
      -webkit-appearance: none;
      background-color: black;
      color: white;
      border: none;
      height: 2em;
      padding: 0 2em;
      outline: 0;

      &.undo {
        -moz-transform: scaleX(-1);    /* Gecko */
        -o-transform: scaleX(-1);      /* Opera */
        -webkit-transform: scaleX(-1); /* Webkit */
        transform: scaleX(-1);         /* Standard */
      }

      &.rearrange {
        float: right;

        svg {
          width: 2em;
        }
      }
      &.restart {
        float: right;
      }
    }
  }

  .players {
    padding: 1rem;

    button.add-player {
      @include sit-green-button;
    }

    .registered-players {
      margin-bottom: 3rem;
    }

    table, th, td {
      border-right: 0;
      border-left: 0;
    }
    tr.player-row {
      padding: 1rem 0 ;

      .player-pos {
        .player-move-up {
          margin: 0 1rem;
          svg {
            transform: rotate(-90deg);
          }
        }
        .player-current-pos {
          font-size: 1.5rem;
        }
        .player-move-down {
          margin: 0 1rem;
          svg {
            transform: rotate(90deg);
          }
        }
      }
    }
  }
}
