.game {
  nav {
    width: 100%;
    background-color: black;
    height: 2em;
    z-index: 1;
    position: fixed;

    svg.icon {
      width: 1em;
    }
    button {
      padding: 0.45em 2em;
    }
  }
  .game-menu {
    position: fixed;
    z-index: 3;
    top: 3em;
    left: 1em;
    background-color: black;
    border-radius: 1em;
    border: solid 1px white;

    >div {
      margin: 0 1em;
    }

    button {
      display: block;
      width: 100%;
      padding: 1em 2em;

      &.undo {
        border-bottom: solid 1px;
      }
    }
  }
  nav button,.game-menu button {
    -webkit-appearance: none;
    background-color: black;
    color: white;
    border: none;
    outline: 0;
  }
}

.game-error {
  padding: 1rem;
  text-align: center;

  button {
    text-decoration: none;
    color: white;
    background-color: #00a900;
    border-radius: 1em;
    border: none;
    padding: 0.5em 1em;
    box-shadow: 2px 2px 1px 0 black;
    outline: none;
    cursor: pointer;
  }
}